.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.title {
  height: 5%;
  margin-top: 5%;
  margin-bottom: 0;
  font-style: italic;
}

.title-header > h4 {
  margin-top: 2.5%;
  margin-bottom: 2.5%;
}

.title-header {
  display: flex;
  width: 80%;
  flex-direction: column;
}

.bar-charts {
  position: relative;
  width: 80%;
  height: 1200px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5%;
  row-gap: 5%;
}

.source-footer {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 10%;
}

.source-border {
  border: 1px solid black;
}

.sources {
  list-style-type: none;
  padding: 0;
}